import React, { Component, useRef, useState, useEffect } from "react";
import Loader from "react-loader-spinner";

import { BrowserRouter, Switch, Route} from "react-router-dom";
import About from "./components/about";
import P from "./components/P";
import Slider from "./components/Slider";
import Images from "./images";

import './index.css';



import ScrollToTop2 from "./components/ScrollToTop2";

import Home from "./components/home/Home";
import { Header } from "./components/Header";
import Clima from "./components/Clima";

import ClimaKey from "./components/ClimaKey";

import { AnimatePresence } from "framer-motion";


class App extends Component {
  render() {
  return (
    <>
     
  <BrowserRouter>

  <Header/>
        <ScrollToTop2>
        <AnimatePresence> 
          <Switch>
            <Route
              exact
              path="/"
              render={(props) => <Home/>}
            />
            <Route
              exact
              path={process.env.PUBLIC_URL + "/about"}
              render={(props) => <About/>}
            ></Route>
            <Route
              exact
              path={process.env.PUBLIC_URL + "/portfolio"}
              render={(props) => <Slider images={Images} />}
            ></Route>
            <Route exact path="/P:num" render={(props) => <P/>}></Route>
            <Route  exact path="/clima" render={(props) => <Clima/>}></Route>
            <Route  exact path="/climakey" render={(props) => <ClimaKey/>}></Route>

          </Switch>
          </AnimatePresence>
        </ScrollToTop2>
        </BrowserRouter>
        
    </>
   )
  }
}

export default App;